.footer {
  background-color: $primary-color;
}

.footer-img {
  width: 255px;
  height: 201px;
}

.footer-wrapper {
  position: relative;
  align-items: center;
  padding-top: 60px;
  padding-bottom: 120px;
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-direction: inherit;
    padding-top: 72px;
    padding-bottom: 105px;
  }
}

.networks-list {
  display: flex;
  margin-bottom: 31px;

  @media screen and (max-width: 767px) {
    justify-content: center;
    margin-bottom: 40px;
  }
}

.info-items {
  color: $btn-color;
}

.info-items:not(:last-child) {
  margin-bottom: 20px;
}

.networks-icon {
  width: 34px;
  height: 34px;
  color: $btn-color;
}

.networks-text {
  color: $btn-color;

  @media screen and (max-width: 767px) {
    text-align: center;
  }
}

.networks-item:not(:last-child) {
  margin-right: 40px;
}

.networks-text:not(:last-child) {
  margin-bottom: 20px;
}

.phone-title {
  color: $btn-color;
  margin-bottom: 20px;
  text-align: center;
  @media screen and (min-width: 768px) {
    display: none;
  }
}

.flex-items {
  @media screen and (max-width: 767px) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.flex-for-mobile {
  display: flex;
  align-items: flex-end;
  @media screen and (max-width: 767px) {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    margin-bottom: 40px;
  }
}

.footer-logo {
  @media screen and (max-width: 767px) {
    position: absolute;
    margin-bottom: 40px;
  }
}

.footer-info {
  @media screen and (max-width: 767px) {
    margin-top: 240px;
    margin-bottom: 40px;
  }
}
